'use client';
import Box from '@mui/material/Box';
import FileCard from './file-card';

const FileView = ({ tabIndex, tabFileGroups }) => {

  console.log(`FileView tabFileGroups:`, tabFileGroups);

  return (
    <>
      {!!tabFileGroups && tabFileGroups.map((group, index: number) => {
        console.log(`group:`, group);
        return (
          <Box key={index} hidden={index !== tabIndex} sx={{ bgColor: 'background.paper' }}>
            {!!group && group.map(card => {
              return (
                <FileCard key={card.key} {...{ thumbLink: card.thumblink, fileName: card.fileName, size: card.size }} />
              )})}
          </Box>
        )})}
    </>
  )
}


export default FileView;
